/* ========================================
   ARTICLE ROTATION ANIMATIONS
   ======================================== */

/* Counter-clockwise rotation keyframes */
@keyframes rotateCounterClockwise {
  0% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
  20% {
    transform: scale(0.9) rotate(-10deg);
    opacity: 0.8;
  }
  50% {
    transform: scale(0.85) rotate(-20deg);
    opacity: 0.6;
  }
  80% {
    transform: scale(0.9) rotate(-10deg);
    opacity: 0.8;
  }
  100% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
}

@keyframes slideFromBig {
  0% {
    transform: translateX(100%) scale(1.2);
    opacity: 0.7;
  }
  100% {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
}

@keyframes slideToPosition {
  0% {
    transform: translateX(-20%) translateY(-20%) scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: translateX(0) translateY(0) scale(1);
    opacity: 1;
  }
}

@keyframes fadeInFromRight {
  0% {
    transform: translateX(50%) scale(0.9);
    opacity: 0;
  }
  100% {
    transform: translateX(0) scale(1);
    opacity: 1;
  }
}

/* Main rotation animation class */
.article-rotation-animation {
  animation: rotateCounterClockwise 1s ease-in-out;
}

/* Position-specific rotation animations */
.rotate-to-big {
  animation: fadeInFromRight 1s ease-out;
}

.rotate-to-top-right {
  animation: slideFromBig 1s ease-out;
}

.rotate-to-top-left {
  animation: slideToPosition 1s ease-out 0.1s both;
}

.rotate-to-bottom-left {
  animation: slideToPosition 1s ease-out 0.2s both;
}

.rotate-to-bottom-right {
  animation: slideToPosition 1s ease-out 0.3s both;
}

/* Rotation position indicators */
.rotation-big::after {
  content: 'üÜï';
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(34, 197, 94, 0.9);
  color: white;
  padding: 4px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: bold;
  z-index: 30;
  animation: pulse 2s infinite;
}

.rotation-top-right::after {
  content: 'üìç';
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 10px;
  z-index: 30;
}

/* New article indicator animation */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1);
  }
}

/* Hover effects during rotation */
.article-rotation-animation:hover {
  transform: scale(1.05) !important;
  transition: transform 0.2s ease-in-out;
}

/* Mobile rotation animations */
@media (max-width: 768px) {
  .article-rotation-animation {
    animation: rotateCounterClockwise 0.8s ease-in-out;
  }
  
  .rotate-to-big,
  .rotate-to-top-right,
  .rotate-to-top-left,
  .rotate-to-bottom-left,
  .rotate-to-bottom-right {
    animation-duration: 0.8s;
  }
}
